body {
  margin: 0px; }

.canvas-wrapper {
  position: absolute;
  width: 50%;
  height: 100%; }
  .canvas-wrapper .title {
    position: absolute;
    width: 100%;
    font-size: 24px;
    text-align: center;
    padding: 10px 0px;
    z-index: 1; }
  .canvas-wrapper canvas {
    position: absolute;
    width: 100%;
    height: 100%; }
  .canvas-wrapper:last-child {
    border-left: 1px solid;
    left: 50%; }
